<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入会员UID、手机号码"
                      v-model="listQuery.queryStr">
            </el-input>
            <el-button class="filter-item" @click="handleFilter" type="primary" style="margin-left: 30px">查询</el-button>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="加载中......."
                    style="width: 100%;">
                <el-table-column
                        type="index"
                        label="日期"
                        :index="indexMethod">
                </el-table-column>
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                        fixed="right"
                        label="操作"
                        width="170">
                    <template scope="scope" class="filter-operation">
                        <ul style="list-style: none;padding: 0px;margin: 0px">
                            <li style="float:left;width: 50px">
                                <el-button
                                        type="text"
                                        @click="handleDeleteEdit(scope.row.userId)">审核
                                </el-button>
                            </li>
                        </ul>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="listQuery.page"
                               :page-sizes="[10,20,30, 50]" :page-size="listQuery.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    //import fetch from '@/utils/fetch'
    import DCTP from '@/utils/dctp';
    import {getlist} from '@/api/system'

    const logintypeOptions = [
        {key: '', display_name: '全部状态'},
        {key: 0, display_name: '未提交'},
        {key: 1, display_name: '待审核'},
        {key: 3, display_name: '已认证'},
        {key: 4, display_name: '不通过'},
    ];

    export default {
        data() {
            return {
                logintypeOptions,
                //总条数
                total: null,
                //返回来列表的数据
                tableData: [],
                //查询条件对象
                listQuery: {
                    queryStr: ''
                },
                msg: '',
                tableHeader: [
                    {
                        label: "UID",
                        prop: "id",
                        width: 65
                    }, {
                        label: "类型",
                        prop: "tradeMappingId",
                        width: 100,
                        /*align: 'center',
                        formatter(row, column, value) {
                            let item = DCTP.utils.findDataByValue(logintypeOptions, value, 'key');
                            return item ? item.display_name : '';
                        }*/
                    }, {
                        label: "价格同步类型",
                        prop: "tradeType",
                        width: 95
                    }, {
                        label: "状态",
                        prop: "status",
                        width: 80,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 1 ? '禁用' : (value === 2 ? '启用' : '参数错误');
                        }
                    }, {
                        label: "交易类型",
                        prop: "tradeMappingId",
                        width: 130
                    }, {
                        label: "最小交易数量",
                        prop: "minQuantity",
                        width: 180,
                    }, {
                        label: "最大交易数量",
                        prop: "maxQuantity",
                    }, {
                        label: "最小浮动价",
                        prop: "minPrice",
                        minWidth: 95,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 0 ? '身份证' : (value === 2 ? '护照' : '未知');
                        }
                    }, {
                        label: "最大浮动价",
                        prop: "maxPrice",
                        width: 95,
                    }, {
                        label: "多少分钟一次",
                        prop: "intervalMinute",
                        width: 95
                    }, {
                        label: "开始前随机停几秒",
                        prop: "suspendSecond",
                        width: 170
                    }, {
                        label: "创建时间",
                        prop: "createTime",
                        width: 170,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }
                ]
            }

        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length
            }
        }
        ,
        methods: {
            getList() {
                this.listLoading = true;
                getlist().then(response => {
                    this.tableData = response.data.data;
                    this.total = response.data.page.count;
                    this.listLoading = false;
                })
            }
        }
        ,
        created() {
            this.getList();
        }
    }
</script>


